<template>
  <div id="uploadImage">
    <div class="main">
      <div class="tt">修改密码</div>
      <el-form :model="form" :rules="rules" style="margin-top:60px;width:60%" ref="form">
        <el-form-item label="原密码:" :label-width="formLabelWidth" prop="oldPwd">
          <el-input show-password v-model="form.oldPwd" clearable></el-input>
        </el-form-item>
        <el-form-item label="新密码:" :label-width="formLabelWidth" prop="newPwd">
          <el-input show-password v-model="form.newPwd" clearable></el-input>
        </el-form-item>
        <el-form-item label="确认新密码:" :label-width="formLabelWidth" prop="confirmPwd">
          <el-input show-password v-model="form.confirmPwd" clearable></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :loading="createLoading1" type="primary" @click="comfirmFormHandler('form')">
          <i class="el-icon-check el-icon--left"></i>保 存
        </el-button>
        <el-button @click="resetForm">
          <i class="el-icon-close el-icon--left"></i>重 置
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import md5 from "js-md5";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    let validatePwd = async (rule, value, callback) => {
      const res = await http.post(api.ComparePwd, {
        pwd: this.form.oldPwd,
        token: http.getToken()
      });
      if (res.type) {
        callback(new Error(res.message));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.newPwd) {
        this.password = false;
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      createLoading1: false,
      TeacherID: "",
      userid: "",
      username: "",
      formLabelWidth: "160px",
      form: {
        oldPwd: "",
        newPwd: "",
        confirmPwd: ""
      },
      rules: {
        oldPwd: [
          {
            required: true,
            validator: validatePwd,
            trigger: "blur"
          }
        ],
        newPwd: [
          { required: true, validator: validatePass, trigger: "blur" },
          { min: 6, max: 32, message: "长度限制为6-32位", trigger: "blur" }
        ],
        confirmPwd: [
          { required: true, validator: validatePass2, trigger: "blur" },
          { min: 6, max: 32, message: "长度限制为6-32位", trigger: "blur" }
        ]
      }
    };
  },
  computed: {},
  watch: {},
  mounted() {
    let userInfo = http.getUser();
    if (userInfo) {
      this.userid = userInfo.UserID;
      this.username = userInfo.UserName;
    }
  },
  methods: {
    // 表单确认,添加用户
    comfirmFormHandler(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savesubmit();
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    savesubmit: async function() {
      this.createLoading1 = true;
      this.saveData({
        pwd: md5(this.form.newPwd),
        Token: http.getToken()
      });
    },
    async saveData(params) {
      const res = await http.post(api.UpdatePwd, params);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "密码修改成功！",
          type: "success"
        });
      }
      this.createLoading1 = false;
    },
    resetForm() {
      this.$refs["form"].resetFields();
      this.$refs.form.clearValidate();
    }
  }
};
</script>

<style lang='less'>
#uploadImage {
  text-align: left;
  width: 956px;
  height: 1017px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  //
  padding: 20px;
  padding-top: 0;
  .tt {
    color: #333;
    font-size: 14px;
    border-bottom: 1px solid #ecf1f7;
    line-height: 50px;
  }
  .main {
    .dialog-footer {
      width: 100%;
      display: block;
      text-align: center;
      margin-top: 36px;
    }
  }
}
</style>


